import React, { Component } from 'react';
import WeeklyAttendAdminItem from './WeeklyAttendAdminItem';

class WeeklyAttendAdminList extends Component {
  constructor(props) {
    super(props);
    this.onSave = this.onSave.bind(this);
  }
  render() {
    return (
      <div className="weekly-attend-list">
        <span className="weekly-title">{this.props.weeklyTitle}</span>
        <ol>
          <WeeklyAttendAdminItem memberName="" weeklyTitle={this.props.weeklyTitle} />
          <WeeklyAttendAdminItem memberName="" weeklyTitle={this.props.weeklyTitle} isGuest />
          <WeeklyAttendAdminItem memberName="" weeklyTitle={this.props.weeklyTitle} />
          <WeeklyAttendAdminItem memberName="" weeklyTitle={this.props.weeklyTitle} />
          <WeeklyAttendAdminItem memberName="" weeklyTitle={this.props.weeklyTitle} />
        </ol>
        <button className="cus-button" onClick={this.onSave}></button>
      </div>
    );
  }

  onSave = () => {
    alert('.');
  }
}

export default WeeklyAttendAdminList;



